import React from "react";
import {BotOutput} from "./BotOutput";
import Delay from "react-delay/lib/Delay";
import {AnimatePresence, motion} from "framer-motion";

export class BotOutputs extends React.Component {
    render() {
        let time = 3000;
        let times = 0;
        let outputElements =
            this.props.outputs.map((output, index, arr) =>

                <Delay key={output.index} wait={(time * times++)}>
                    <motion.div initial={false} exit={{y: -300, opacity: 0}} transition={{duration: 0.5}}>
                        <motion.div initial={{y: 300}} animate={{y: -100}} transition={{duration: 2}}>
                            <BotOutput output={output}
                                       startQuickReplyAnimation={index === arr.length - 1 ?
                                           this.props.startQuickReplyAnimation : () => {}}/>
                        </motion.div>
                    </motion.div>
                </Delay>
            );

        return (
            <span className="botOutputsContainer">
                <AnimatePresence>
                    {outputElements}
                </AnimatePresence>
            </span>
        );
    }
}
