import PowerModeInput from "power-mode-input";
import React from "react";
import axios from 'axios';
import {eddiApiUrl} from "./App";

export class InputField extends React.Component {
    constructor(props) {
        super(props);
        this.inputRef = props.inputRef;
    }

    componentDidMount() {
        PowerModeInput.make(this.inputRef.current, {
            height: 2,
            tha: [0, 360],
            g: 0.1,
            num: 1,
            radius: 2,
            circle: true,
            alpha: [0.75, 0.1],
            color: "#67AB67"
        });
    }

    sayToBot = async () => {
        let userInput = this.inputRef.current.value;
        if (userInput !== '') {
            let res = await axios.post(eddiApiUrl, {input: userInput});
            let conversationOutputs = res.data.conversationOutputs;

            if (conversationOutputs.length > 0) {
                let conversationOutput = conversationOutputs[0];
                this.props.updateBotOutputs(conversationOutput);
            }

            //reset inputField
            this.inputRef.current.value = "";
        }
    };

    keyPress = (e) => {
        if (e.keyCode === 13) {
            this.sayToBot();
        }
    };

    /*setStringToType = (instance) => {
        console.log("setStringToType YEAH");
        instance.type(this.props.userInput);
        return instance;
    };*/

    render() {
        return (
            <div className="inputFieldContainer">
                <input id="userInputField" autoFocus
                       onKeyDown={this.keyPress}
                       autoComplete="off"
                       ref={this.inputRef}
                       className="inputField"
                       tabIndex={1}/>
                <button type="submit" onClick={this.sayToBot}>reply</button>
            </div>
        );
    }
}
