import React from "react";
import {QuickReply} from "./QuickReply";
import Delay from "react-delay/lib/Delay";
import {FontAwesomeIcon} from "@fortawesome/react-fontawesome";
import {faUndo} from "@fortawesome/free-solid-svg-icons/faUndo";
import axios from "axios";
import {eddiApiUrl} from "./App";

let isUndoAvailable = async () => {
    let res = await axios.get(eddiApiUrl + '/undo');
    console.log("undo available", res.data);
    return res.data === true;
};

let undo = async () => {
    console.log("undo pressed!");
    await axios.post(eddiApiUrl + '/undo');
};

export class QuickReplies extends React.Component {
    onClickUndo = () => {
        undo();
        this.props.loadConversationLog();
    };

    render() {
        let time = 200;
        let times = 1;
        let quickReplyElements = this.props.quickReplies.map((quickReply, index) =>
            <Delay key={quickReply.index} wait={time * times++}>
                <QuickReply
                    quickReply={quickReply}
                    inputRef={this.props.inputRef}
                    removeQuickReplies={this.props.removeQuickReplies}
                    tabIndex={index + 2}/>
            </Delay>
        );
        /*<FontAwesomeIcon icon={faUndo} onClick={this.onClickUndo} className="undoIcon"/>*/
        /*<i className="fas fa-arrow-left"></i>*/
        let ret = <span/>;
        if (this.props.startQuickReplyAnimation) {
            if (isUndoAvailable()) {
                ret =
                    <div className="quickReplyContainer">

                        {quickReplyElements}
                    </div>
            } else {
                ret =
                    <div className="quickReplyContainer">
                        {quickReplyElements}
                    </div>
            }

        }

        return (ret);
    }
}
